@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("http://fonts.googleapis.com/css?family=Roboto:400,300,100,600,200,900,,800,500,700");

@layer base {
  :root {
    --color-text-base: #3fa333;
    --color-bg-base: #fafaf9;

    --color-connect-text-50: #fafaf9;
    --color-connect-text-100: #f5f5f4;
    --color-connect-text-200: #e7e5e4;
    --color-connect-text-300: #d6d3d1;
    --color-connect-text-400: #a8a29e;
    --color-connect-text-500: #78716c;
    --color-connect-text-600: #57534e;
    --color-connect-text-700: #44403c;
    --color-connect-text-800: #292524;
    --color-connect-text-900: #1c1917;
  }
}

.dark {
  --color-connect-text-900: #fafaf9;
  --color-connect-text-800: #f5f5f4;
  --color-connect-text-700: #e7e5e4;
  --color-connect-text-600: #d6d3d1;
  --color-connect-text-500: #a8a29e;
  --color-connect-text-400: #78716c;
  --color-connect-text-300: #57534e;
  --color-connect-text-200: #44403c;
  --color-connect-text-100: #292524;
  --color-connect-text-50: #292524;
}

@font-face {
  font-family: "GmarketSansLight";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansLight.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansMedium";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "GmarketSansBold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: "Roboto", sans-serif;
}

#root,
html {
  width: 100%;
  height: 100%;
  min-height: 100%;
  @apply bg-text-50;
}
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.a-char-button {
  @apply font-bold rounded-md bg-background-400 hover:bg-point-500 max-h-[20px] text-sm flex items-center 
  transition-all p-1 text-text-50 text-center hover:rotate-6 w-5 h-5 min-w-[5] max-w-[5];
}

a {
  @apply whitespace-normal break-words text-text-500 italic 
  hover:text-text-700 transition-all;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @apply font-bold not-italic  no-underline py-1 text-text-800;
}

h1 {
  @apply text-3xl;
}

h2 {
  @apply text-2xl;
}

h3 {
  @apply text-xl;
}

h4 {
  @apply text-base;
}

h5 {
  @apply text-sm;
}

h6 {
  @apply text-xs;
}

blockquote {
  @apply p-2  pl-3 border-text-400 border-l-4 pb-4 my-2;
}

ul {
  @apply list-disc;
}
ol {
  @apply list-decimal;
}
p {
  @apply py-1;
}

ul,
ol {
  @apply ml-4;
}

li {
  @apply py-1;
}

.connect-input {
  @apply rounded-sm border border-text-400 text-text-800 focus:outline-none bg-background-50;
}

.input-label {
  @apply mr-2 text-sm;
}

.editable-placeholder {
  @apply bg-background-200;
}

.editable-placeholder:empty:before {
  content: attr(placeholder);
  color: grey;
  display: inline-block;
}

.editable-placeholder:focus {
  outline: none;
}
